.header {
  position: sticky;
  z-index: 15;
  top: 0;
  left: 0;

  width: 100%;
  padding: 30px 0 35px;

  background: $black-pearl-to-transparent;

  &--nojs {
    position: relative;
  }

  @media (max-width: $vp-1023) {
    padding: 20px 0 25px;
  }

  @media (max-width: $vp-767) {
    padding: 15px 0 20px;
  }
}

.header__logo {
  @media (max-width: $vp-767) {
    width: 35px;
    height: 35px;
  }
}

.container--header a {
  display: block;
  width: 160px;
  margin-top: -5px;
  padding: 10px 15px 13px 17px;

  @include roboto-bold($size-14, $line-16);
  text-transform: uppercase;
  letter-spacing: 0.05em;

  color: $color-alice-blue;
  border: 1px solid $color-alice-blue;
  border-radius: 3px;

  @media (max-width: $vp-1023) {
    display: none;
  }
}

.header__contacts {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 10px;
  margin-left: -4px;

  @media (max-width: $vp-1023) {
    justify-content: flex-end;
    margin-left: 0;
  }

  @media (max-width: $vp-767) {
    margin-top: 5px;
  }
}

.header__contacts a {
  position: relative;

  width: 208px;
  padding: 0 0 0 35px;

  @include ptmono($size-16, $line-18);
  letter-spacing: normal;
  text-transform: none;

  border: none;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    width: 20px;
    height: 20px;

    background-image: url("../img/sprite/icon-mail.svg");
    background-position: center;
    background-repeat: no-repeat;
  }

  &:nth-of-type(2n) {
    margin-left: 44px;
    margin-bottom: 15px;

    &::before {
      background-image: url("../img/sprite/icon-phone.svg");
    }
  }

  @media (max-width: $vp-1058) {
    &:nth-of-type(2n) {
      margin-left: 0;
    }
  }

  @media (max-width: $vp-1023) {
    display: block;
    width: 0;
    height: 20px;
    margin: 0;
    padding: 0 0 0 50px;
    overflow: hidden;

    color: $color-transparent;

    &:nth-of-type(2n) {
      margin-bottom: 0;
      margin-right: 0;
      margin-left: 10px;
    }
  }

  @media (max-width: $vp-767) {
    &:nth-of-type(2n) {
      padding-left: 40px;
    }
  }
}
